.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 22px;
  line-height: 27px;
  color: #ffffff;
  text-transform: uppercase;
  padding: 10px 10px 20px 10px;
}
.closeIcon {
  padding: 0px;
  width: 12px;
  height: 12px;
}

.itemWrapper {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr;
  p {
    font-size: 16px;
    font-weight: 700;
    color: white;
  }
}
